
'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Check, Loader2, Star } from 'lucide-react';
import { useAuth } from '@/hooks/useAuth';
import { useToast } from '@/hooks/use-toast';
import { useRouter } from 'next/navigation';
import { usePageBackground } from '@/hooks/usePageBackground';
import { cn } from '@/lib/utils';

const includedFeatures = [
  'Unlimited Practice Tests',
  'All Difficulty Levels',
  'Detailed Progress Tracking',
  'No Advertisements',
  'Priority Support',
];

export default function PricingPage() {
  usePageBackground('https://placehold.co/1920x1080/e0e7ff/3730a3?text=.');
  const { user, profile, upgradeToPro } = useAuth();
  const { toast } = useToast();
  const router = useRouter();
  const [isUpgrading, setIsUpgrading] = useState(false);

  const handleUpgrade = async () => {
    if (!user) {
      router.push('/login?redirect=/pricing');
      return;
    }
    setIsUpgrading(true);
    try {
      await upgradeToPro();
      toast({
        title: 'Upgrade Successful!',
        description: 'Welcome to Abacus Pro! You now have access to all features.',
      });
      router.push('/tests');
    } catch (error) {
      toast({
        title: 'Upgrade Failed',
        description: 'Something went wrong. Please try again.',
        variant: 'destructive',
      });
    } finally {
      setIsUpgrading(false);
    }
  };

  const isAlreadyPro = profile?.subscriptionStatus === 'pro';

  return (
    <div className="max-w-4xl mx-auto">
      <div className="text-center mb-12">
        <h1 className="text-4xl font-bold tracking-tight text-foreground font-headline sm:text-5xl">
          Unlock Your Full Potential
        </h1>
        <p className="mt-4 max-w-2xl mx-auto text-lg text-muted-foreground">
          Choose the plan that's right for you and start mastering mental math today.
        </p>
      </div>
      
      <div className="flex justify-center">
        <Card className="w-full max-w-md shadow-2xl border-2 border-primary scale-105">
           <CardHeader className="text-center bg-primary/10 p-6">
                <Star className="w-12 h-12 text-primary mx-auto mb-4" />
                <CardTitle className="text-3xl font-bold">Abacus Pro</CardTitle>
                <CardDescription className="text-lg">
                    Everything you need to become a math whiz.
                </CardDescription>
                <p className="mt-4">
                    <span className="text-5xl font-extrabold tracking-tight text-foreground">$10</span>
                    <span className="text-base font-medium text-muted-foreground">/month</span>
                </p>
           </CardHeader>
           <CardContent className="p-8">
                <ul className="space-y-4">
                    {includedFeatures.map((feature) => (
                        <li key={feature} className="flex items-center gap-3">
                            <Check className="h-6 w-6 text-green-500" />
                            <span className="text-muted-foreground">{feature}</span>
                        </li>
                    ))}
                </ul>
           </CardContent>
           <CardFooter>
            <Button 
              size="lg" 
              className="w-full"
              onClick={handleUpgrade}
              disabled={isUpgrading || isAlreadyPro}
            >
              {isUpgrading && <Loader2 className="mr-2 h-5 w-5 animate-spin" />}
              {isAlreadyPro ? 'You are a Pro Member' : 'Upgrade to Pro'}
            </Button>
           </CardFooter>
        </Card>
      </div>
    </div>
  )
}
